#login-page {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #def3f4;
  z-index: 99;
}

#login-page .container {
  position: relative;
  width: 240px;
  height: 300px;
  padding: 100px 40px 40px 40px;
  box-sizing: content-box;
}

#login-page .box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 90px 40px 40px 40px;
  backface-visibility: hidden;
  background: linear-gradient(
    230deg,
    rgba(53, 57, 74, 0) 0%,
    rgb(0, 0, 0) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='rgba(53, 57, 74, 0)', endColorstr='rgb(0, 0, 0)',GradientType=1 );
  box-shadow: -15px 15px 15px rgba(0, 0, 0, 0.4);
  transition: all 1s;
}
#login-page .showBox {
  transform: rotateY(0deg);
}
#login-page .hiddenBox {
  transform: rotateY(180deg);
}
#login-page .loginBtn {
  padding: 10px 50px;
  border: 2px solid #4fa1d9;
  border-radius: 50px;
  background: transparent;
  font-size: 11px;
  color: #4fa1d9;
  transition: all 0.2s;
}
#login-page .bottom {
  display: flex;
  height: 42px;
  justify-content: space-between;
  align-items: center;
}
#login-page .registerBtn {
  color: #d3d7f7;
}
#login-page .registerBtn:hover {
  color: #4fa1d9;
  cursor: pointer;
}
#login-page .loginBtn:hover {
  color: white;
  background: #4fa1d9;
  cursor: pointer;
}
#login-page .title {
  height: 60px;
  color: #d3d7f7;
  font-size: 16px;
  margin-bottom: 0;
}
#login-page .footer {
  position: absolute;
  bottom: 20px;
  left: 35px;
  width: 250px;
  color: #d3d7f7;
  font-size: 10px;
}

/*覆盖antd的默认样式*/
#login-page input {
  color: #61bfff;
  border: none;
  outline: none;
  box-shadow: none;
  background: transparent;
}
#login-page .ant-input-group-addon {
  background: transparent;
  padding: 0;
  border: none;
  color: #fff;
  opacity: 0.8;
}
#login-page .ant-input-group-addon .iconfont {
  display: inline-block;
  width: 30px;
  transition: all 0.3s;
  opacity: 0.6;
}
#login-page .ant-form-item {
  margin-bottom: 10px;
}
#login-page .ant-form-explain {
  position: absolute;
  z-index: 99;
  left: 110%;
  top: 0;
  height: 41px;
  /*box-shadow: -2px 2px 2px rgba(0,0,0,.3);*/
}
.login-notification {
  background: transparent;
  border: 1px solid #d3d7f7;
  color: #d3d7f7;
  width: 250px;
  height: 80px;
  float: right;
  margin-right: 20px;
}
.login-notification .ant-notification-notice-message {
  color: #d3d7f7;
}
.login-notification .ant-notification-notice-close {
  color: #d3d7f7;
}
/*更改谷歌浏览器input背景*/
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  /*延迟背景颜色载入*/
  -webkit-transition-delay: 99999s;
  -webkit-transition: color 99999s ease-out, background-color 99999s ease-out;
}
